e(1.2) translateY(8px);transform:scale(1.2) translateY(8px)}50%{-webkit-transform:translateY(-4px);transform:translateY(-4px)}100%{-webkit-transform:scale(1) translateY(0);transform:scale(1) translateY(0)}}.details__contact__phone,.details__contact__mail{display:flex;align-items:center}.details__contact__phone__icon,.details__contact__mail__icon,.details__contact__phone__icon--active,.details__contact__mail__icon--active{margin-right:8px}
@media (min-width: 1000px) {
  .outline {
    position: fixed;
    top: 0;
    left: 0;
    width: 22px;
    height: 22px;
    border: 2px solid rgba(31, 30, 30, 0.808);
    border-radius: 100%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    transition-duration: 100ms;
    transition-timing-function: ease-out;
    will-change: width, height, transform, border;
    z-index: 999;
    pointer-events: none;
  }

  .cursor {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 8px;
    height: 8px;
    background-color: black;
    border-radius: 100%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 999;
    pointer-events: none;
  }

  .outline.hovered {
    width: 50px;
    height: 50px;
    border-width: 3px;
    border-color: lightgray;
  }

  .cursor.hovered {
    display: none;
  }
}
@media (max-width: 1000px) {
  .cursor,
  .outline {
    display: none;
  }
}

.header{display:flex;justify-content:center;align-items:center;grid-area:a}.header__title{margin:32px 16px 32px 0}
